// This is the main Sass file for the MillerApps website.
// It contains variables, mixins, and imports for the site's styles.
// The file also includes CSS rules for centering elements and displaying images.

@charset "utf-8";

// Our variables
$base-font-family: Helvetica, Arial, sans-serif;
$base-font-size:   16px;
$small-font-size:  $base-font-size * 0.875;
$medium-font-size: $base-font-size * 1.111;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #fab634;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

// Width of the content area
$content-width:    1000px;

$on-palm:          600px;
$on-laptop:        800px;

// Using media queries with like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}

// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "base",
        "layout",
        "syntax-highlighting",
        "nav",
        "work"
;

// CSS rules for centering an unordered list
#center ul {
	margin-left: auto;
  margin-right: auto;
	padding: 0;
	list-style: none;
}

// CSS rules for floating list items
.apps li{
	float: left;
}

// CSS rules for an image holder
.image-holder img {
	height: 300px;
	width: 300px;
	float: right;
	display: block;
	padding: 10px;
}

// CSS rules for centering an image
.center-image {
    margin: 0 auto;
    display: block;
}
